.password_match {
  display: none;
  border: 1px solid #444;
  position: absolute;
  box-shadow: 0 2px 5px 2px rgba(0,0,0,.3);
  background-color: rgb(33,37,41);
  z-index: 99;
  padding: 10px;
  font-size: 12px;
}
.active {
  display: block;
}
.no_match {
  color: tomato;
}
.match {
  color: green;
}