@import '../../../styles/constants.module';

.sign_in {
    background-image: url('https://wallup.net/wp-content/uploads/2016/01/230662-minimalism-simple-simple_background-abstract.jpg');
    background-repeat: no-repeat;
    background-position: 0 0;
    height: 100%;
}

.error {
    font-size: 12px;
    color: tomato;
    position: absolute;
    bottom: -35px;
}

.row {
    position: relative;
}

.link {
    color: $colorBlue;
    text-decoration: none;
    padding: 0 5px;
    @media (max-width: 400px) {
        font-size: 12px;
    }
}
.checkbox {
    margin-right: 10px;
}

.eye_icon {
    position: absolute;
    right: 20px;
    top: 25px;
    cursor: pointer;
    color: #fff;
}

.relative {
    position: relative;
}

.gradient {
    background: rgb(167,184,226);
    background: linear-gradient(90deg, rgba(167,184,226,1) 0%, rgba(174,218,238,1) 0%, rgba(148,187,233,1) 100%);
    @media (max-width: 1024px) {
        display: none !important;
    }
}

.authFormWrap {
    display: flex;
    width: 100%;
    justify-content: space-between;
}
.authFormItem {
    @media (max-width:1024px) {
        position: relative;
        bottom: 5%;
        left: 0;
        right: 0;
    }
}
.authItemImg {
    @media (max-width:1024px) {
        display: flex;
        align-items: flex-start;
    }
}